//singer-detail

export default class Song {
    constructor({songmid,songname,songid,singer,albummid,albumname,image,duration}) {   //constructor
        this.songmid = songmid;   //id
        this.songname = songname;  //
        this.songid = songid;  //id
        this.singer = singer;  //
        this.albummid = albummid;  //id
        this.albumname = albumname;  //
        this.image = image;
        this.duration = duration;  //
    }
} 

//singer-detaill
export const createSong = (musicData) => {
    return new Song({
        songmid: musicData.songmid,  //id
        songname: musicData.songname, //
        songid: musicData.songid, //id
        singer: getsingerName(musicData.singer), //
        albummid: musicData.albummid, //id
        albumname: musicData.albumname, //
        duration:musicData.interval,  //
        image:`https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`
        // url:`http://dl.stream.qqmusic.qq.com/C400${musicData.songmid}.m4a?guid=6711929420&uin=0&fromtag=38&vkey=${vkey}`
    })
}

//
export const createSongSheet = (musicData) => {
    return new Song({
        songmid: musicData.mid,  //idid
        songname: musicData.album.name, //
        songid: musicData.id, //id
        singer: getsingerName(musicData.singer), //
        albumname: musicData.name, //
        duration:musicData.interval,  //
        image:`https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.album.mid}.jpg?max_age=2592000`,
        // url:`http://dl.stream.qqmusic.qq.com/C400${musicData.songmid}.m4a?guid=6711929420&uin=0&fromtag=38&vkey=${vkey}`
    })
}

//
export const getsingerName = (singers) => {
    let ret = [];
    if(!singers) {
        return '';
    }
    singers.forEach(singer => {
        if(singer.name) {
            ret.push(singer.name);
        }
    })
    return ret.join('');
}


