import {mapGetters,mapActions,mapMutations} from 'vuex'

//padding-bottom= 60px,
export const playListMixin = {
    computed: {
        ...mapGetters([
            'playList'
        ])
    },
    mounted() {//mountedactivatedscroll
        if(this.playList.length > 0) {
            this.changeListBottom(this.playList);
        }
    },
    activated() {
        if(this.playList.length > 0) {
            this.changeListBottom(this.playList);
        }
    },
    watch: {
        playList(newList) {
            this.changeListBottom(newList);
        }
    },
    methods: {
        changeListBottom(list) {
            throw new Error ('changeListBottom')
        }
    }
};


/*--------------------playerplayListJS------------------------ */
import {playMode} from 'js/config'
import {shuffle} from 'js/util'

export const playerMixin = {
    computed: {
        playModeIcon() { //
            return this.playMode === playMode.sequence ? 'icon-sequence' : this.playMode === playMode.random ? 'icon-random' : 'icon-loop' ;
        },
        ...mapGetters([
            'playMode',
            'sequenceList',
            'curSong',
            'playList',
            'likeList'
        ])
    },
    methods: {
        changePlayMode() {  //
            const mode = (this.playMode+1) % 3;
            this.setPlayMode(mode);
           
            let list = null;
            if(this.playMode === playMode.random) { //
                list = shuffle(this.sequenceList);
            } else {  //
                list = this.sequenceList;
            }

            this.resetCurIndex(list);  //
            this.setPlayList(list);
        },
        resetCurIndex(list) {  //
            let index = list.findIndex((item) => {
                return item.songid === this.curSong.songid; //id
            });
            this.setCurIndex(index);
        },
        /* ---- */
        getFavoriteIcon(curSong) {
            if(this.isFavoriteSong(curSong)) { //
                return 'icon-favorite';
            } 
            return 'icon-not-favorite'; //
            
        },
        toggleFavorite(curSong) {
            if(this.isFavoriteSong(curSong)) { //
                this.deleteLikeSong(curSong);
            } else {
                this.saveLikeList(curSong); //
            }
        },
        isFavoriteSong(curSong) {  //
            let index = this.likeList.findIndex((item) => {
                return item.songid === curSong.songid;
            });
            return index > -1;
        },
        ...mapMutations({
            setCurIndex: 'SET_CUR_INDEX',
            setPlayMode: 'SET_PLAY_MODE',
            setPlayList: 'SET_PLAY_LIST',
            setPlaying: 'SET_PLAYING',
        }),
        ...mapActions([
            'saveLikeList',
            'deleteLikeSong'
        ])
    }
};



/* --------------------addPlayListsearchJS----------------------- */
export const searchMixin = {
    data() {
        return {
            listShow: false,
            inputText:''
        }
    },
    methods: {
        changeInputText(newText) {
            this.listShow = newText ? true : false;
            this.inputText = newText;
        },
        beforeScrollStart() { //
            this.$refs.input.blur();
        },
        setInputText(text) { //input
            this.$refs.input.setInputText(text)
        },
        joinHistory() { //
            this.saveHistorySearch(this.inputText);//vuex
        },
        ...mapActions([
            'saveHistorySearch'     
        ])
    },
    computed: {
        ...mapGetters([
            'historySearch'
        ])
    }
}





