import '@babel/polyfill' //ES6
import 'styles/index.styl'  //css

import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index'   //vuex

import fastClick from 'fastclick'  //300
fastClick.attach(document.body);

import VueLazyload from 'vue-lazyload'   //
Vue.use(VueLazyload, {
  preLoad: 1.3,   //1.1
  error: require('./assets/styles/img/error.png'),       //
  loading: require('./assets/styles/img/default.png'),  //
  attempt: 1          //
})

import VueAwesomeSwiper from 'vue-awesome-swiper' //
import 'swiper/dist/css/swiper.css'  //CSS2.6.0
Vue.use(VueAwesomeSwiper)  //

import './assets/js/rem'  //rem 


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



