<template>
    <div class="player" v-if='playList.length>0'>
        <!--  -->
        <transition name='max'
                    @enter='enter'
                    @after-enter='afterEnter'
                    @leave='leave'
                    @after-leave='afterLeave'>
            <div class="maxPlayer" v-show='fullScreen'>
                <div class="maxPlayer-bgImg">
                    <img :src="curSong.image" class="bgImg">
                </div>
                <div class="maxPlayer-header">
                    <i class="iconfont icon-back" @click='goMinPlayer'></i>
                    <h1 class="maxPlayer-title">{{curSong.songname}}</h1>
                    <h3 class="maxPlayer-name">{{curSong.singer}}</h3>
                </div>
                <div class="maxPlayer-middle"
                     @touchstart.prevent='touchstart'
                     @touchmove.prevent='touchmove'
                     @touchend='touchend'>
                    <!--  -->
                    <div class="middle-l" ref='middle_l'>
                        <div class="cd-wrapper" ref='cdWrapper'>
                            <div class="cd" :class='imgRotate'><!--  -->
                                <img :src="curSong.image" class="cd-Img">
                            </div>
                        </div>
                        <div class="play-lyric-wrapper">
                            <div class="play-lyric" v-if='curLineText'>{{curLineText}}</div>
                        </div>
                    </div>
                    <!--  -->
                    <v-scroll class="middle-r" ref='scroll'
                              :data='curSongLyric && curSongLyric.lines'>
                        <div class="lyric-wrapper">
                            <ul class="lyric" v-if='curSongLyric'>
                                <li ref='lyricing'
                                    class="lyric-list" 
                                    v-for='(line,index) of curSongLyric.lines'
                                    :key='index'
                                    :class='{"activeLyric": curLineNum === index}'>{{line.txt}}</li>
                            </ul>
                        </div>
                    </v-scroll>
                </div>
                <div class="maxPlayer-footer">
                    <!-- Bar -->
                    <div class="dot-wrapper">
                        <span class="dot" :class="{'active': currentDot === 'cd'}"></span>
                        <span class="dot" :class="{'active': currentDot === 'lyric'}"></span>
                    </div>
                    <!--  -->
                    <div class="progress-wrapper">
                        <span class="start-time time">{{_getTime(curTime)}}</span>
                        <div class="progress-bar-wrapper">
                            <v-progressBar :speed='speed' @touchend='progressTouchend' @changeTime='changeTime'></v-progressBar>
                        </div>
                        <span class="end-time time">{{_getTime(curSong.duration)}}</span>
                    </div>
                    <!--  -->
                    <div class="maxPlayer-btn">
                        <div class="icon i-left" @click='changePlayMode'>
                            <i class="iconfont" :class='playModeIcon'></i>
                        </div>
                        <div class="icon i-left" :class='errorCls' @click='prev'>
                            <i class="iconfont icon-prev"></i>
                        </div>
                        <div class="icon i-center" @click='togglePlaying'>
                            <i class="iconfont" :class='playingIcon'></i>
                        </div>
                        <div class="icon i-right" :class='errorCls' @click='next'>
                            <i class="iconfont icon-next"></i>
                        </div>
                        <div class="icon i-right">
                            <i class="iconfont"
                               :class='getFavoriteIcon(curSong)'
                               @click='toggleFavorite(curSong)'></i>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <!--  -->
        <div class="minPlayer" v-show='!fullScreen' @click='goMaxPlayer'>
            <div class="minPlayer-bgImg">
                <div class="cd-wrapper">
                    <div class="cd" :class='imgRotate'>
                        <img :src="curSong.image" class="cd-Img">
                    </div>
                </div>
            </div>
            <div class="minPlayer-desc">
                <h3 class="name">{{curSong.songname}}</h3>
                <p class="singer">{{curSong.singer}}</p>
            </div>
            <div class="icon">
                <v-progressCircle :speed='speed'>
                    <i class="iconfont" :class='playingIcon' @click.stop='togglePlaying'></i>
                </v-progressCircle>
            </div>
            <div class="icon">
                <div class="iconfont icon-playlist" @click.stop='goPlayList'></div>
            </div>
        </div>

        <!--  -->
        <audio ref='audio'
               :src='playUrl'
               @timeupdate="timeUpDate"
               @play='ready'
               @ended='playEnd'
               @error="playError"></audio>
        
        <v-playList ref='playList'></v-playList>
    </div>
</template>

<script>
import Scroll from 'common/scroll/scroll'
import ProgressBar from './components/progress-bar';
import ProgressCircle from './components/progress-circle'
import PlayList from 'pages/playList/playList'
import {mapGetters,mapMutations,mapActions} from 'vuex';
import {getSongVkey} from 'api/getSongVkey';
import animations from 'create-keyframe-animation'  //JSCSS3
import {playMode} from 'js/config'   // 
/*import {shuffle} from 'js/util'  //  */
import {prefixStyle} from 'js/dom'
import {getLyric} from 'api/getLyric'
import Lyric from 'lyric-parser'        //
import {playerMixin} from 'js/mixin'

const transform = prefixStyle('transform');
const transitionDuration = prefixStyle('transitionDuration');

export default {
    mixins:[playerMixin],
    data() {
        return {
            playUrl: '',  //
            canplay: true ,  //
            curTime: 0 ,   //
            currentDot: 'cd',  //cd
            curSongLyric: null, //
            curLineNum:0,   //
            curLineText:'' //
        }
    },
    created() {
        this.fontSize = parseFloat(document.documentElement.style.fontSize);
        this.touch = {};
    },
    computed: {  
        playingIcon() { //
            return this.playing ? 'icon-pause' : 'icon-play';
        },

       /*  playModeIcon() { //
            return this.playMode === playMode.sequence ? 'icon-sequence' : this.playMode === playMode.random ? 'icon-random' : 'icon-loop' ;
        }, */

        speed() {
            return (this.curTime / this.curSong.duration) * 100;//
        },

        errorCls() {  //this,canplayfalse
            return this.canplay ? '' : 'errorClass';
        },

        imgRotate() { //class
            return this.playing ? 'play' : 'play pause';
        },

        //Dom
        ...mapGetters([
            'playing',
            'fullScreen',
            /* 'sequenceList', */
            /* 'playList', */
            'curIndex',
            /* 'curSong', */
            /* 'playMode' */
        ])
    },
    watch: {
         //KEY
        curSong(newSong,oldSong) {  //Vkey
            if(!newSong.songid) { //
                return;
            }
              
            //
            if(newSong.songid === oldSong.songid) {  
                return;
            }
            
            getSongVkey(this.curSong.songmid).then(res => {
                if(res) {
                    let vkey = res.items[0].vkey;
                   if(!vkey) {
                       return;
                   }
                    this.playUrl = `http://dl.stream.qqmusic.qq.com/C400${this.curSong.songmid}.m4a?guid=6711929420&uin=0&fromtag=38&vkey=${vkey}`;
                   
                    //
                    this.initLyric();
                    clearTimeout(this.timer);
                    this.timer = setTimeout(() => {
                        this.$refs.audio.play();//
                        this.setPlaying(true); //true                      
                    },500)
                }
            });
            this._getCurSongLyric(); //
        },
        playing(newPlaying) {  //
            const audio = this.$refs.audio;
            if(this.playUrl) {  //
                newPlaying ? audio.play() : audio.pause();
            }
        }
    },
    methods: {
        /* -------------------------------------------- */
        initLyric() {
            this.curSongLyric = null;
            this.curLineNum = 0 ;
            this.curLineText = '';
        },
        _getCurSongLyric() {
            getLyric(this.curSong.songmid).then(lyric => {
                if(lyric) {
                    this.curSongLyric = new Lyric(lyric,this._lyricCallback);
                    if(this.playing) {
                        this.curSongLyric.play();
                    }
                }
            }).catch(err => {
                if(err) {  //
                    this.initLyric();
                }
            })
        },
        _lyricCallback({lineNum,txt}) {
            if(!lineNum) {
                return;
            }
            if(!this.$refs.scroll) {
                return;
            }

            this.curLineNum = lineNum; //index
            if(lineNum > 5) { //5scroll
                let curLineEl = this.$refs.lyricing[lineNum - 5];
                this.$refs.scroll.scrollToElement(curLineEl,1000);
            } else {
                 this.$refs.scroll.scrollTo(0,0,1000);
            }

            this.curLineText = txt ; //
        },

        /* ---------------------------------------------------------------- */
        goMinPlayer() {  //
            this.setFullScreen(false);
        },
        goMaxPlayer() {
            this.setFullScreen(true);
        },

        /* ---------------------------------------------------------------- */
        ready() {   //
            this.canplay = true;

            //
            this.savePlayingSong(this.curSong);
        },
        togglePlaying() {  //or
            this.setPlaying(!this.playing);

            if(this.curSongLyric) {  //API
                this.curSongLyric.togglePlay();
            }
        },
        prev() {  //playList,sequenceList
            if(!this.canplay) {
                return;
            }

            if(this.playList.length === 1) {
                this.loop();
                return;
            }

            let prevIndex = this.curIndex - 1 < 0 ? this.playList.length - 1 : this.curIndex - 1;
            this.setCurIndex(prevIndex);

            this.canplay = false;
        },
        next() {
            if(!this.canplay) {
                return;
            }

            if(this.playList.length === 1) {
                this.loop();
                return;
            }

            let nextIndex = this.curIndex + 1 > this.playList.length - 1 ? 0 : this.curIndex + 1;
            this.setCurIndex(nextIndex);

            this.canplay = false;
        },
        loop() {  //0
            const audio =  this.$refs.audio; 
            audio.currentTime = 0 ;
            audio.play();

            if(!this.playing) {  //
                this.togglePlaying(true);
            }
            if(this.curSongLyric) {  //
                this.curSongLyric.seek(0);
            }
        },
        playEnd() { //
            // 
            if(this.playMode === playMode.loop) { //
                this.loop();
                return;
            }

            this.next();
        },
        playError() {// true
           //canplayfalse,true,truefalse,true
           //falsefalsetrue
            this.canplay = true;
        },
       /*  changePlayMode() {  //
            const mode = (this.playMode+1) % 3;
            this.setPlayMode(mode);
           
            let list = null;
            if(this.playMode === playMode.random) { //
                list = shuffle(this.sequenceList);
            } else {  //
                list = this.sequenceList;
            }

            this.resetCurIndex(list);  //
            this.setPlayList(list);
        },
        resetCurIndex(list) {  //
            let index = list.findIndex((item) => {
                return item.songid === this.curSong.songid; //id
            });
            this.setCurIndex(index);
        }, */
        goPlayList() {  //
            this.$refs.playList.show();
        },

        /* ---------------------------------------------------------------- */
        timeUpDate() { //
             const audio = this.$refs.audio;
             if(!audio) {
                 return;
             }
             this.curTime = audio.currentTime;
        },
        _getTime(time) { //
            let m = parseInt(time/60);
            let s = Math.floor(time%60);
            s = s < 10 ? '0'+s : s;
            return m + ':' + s ;
        },
        progressTouchend(speed){ //
            if(speed > 0) {
                this.$refs.audio.currentTime = this.curSong.duration * speed;
                
                if(this.curSongLyric) {  //,
                    this.curSongLyric.seek(this.$refs.audio.currentTime*1000);
                }
            }
            if(!this.playing) {  //
                this.setPlaying(true);
            }
        },
        changeTime(speed) { //
            if(speed > 0) {
                this.curTime = this.curSong.duration * speed
            }
        },

        /* ------------------------------------------------------------ */
        enter(el,done) {  //
            const {x,y,imgScale} = this._getPosition();
            let animation = {
                0: {
                    transform: `translate3d(-${x}px,${y}px,0) scale(${imgScale})`  //,
                },
                50: {
                    transform: `translate3d(0,0,0) scale(1.1)`
                },
                100: {
                    transform: `translate3d(0,0,0) scale(1)`
                }
            };
            this._inAnimation('in',animation,done); //
        },
        afterEnter() {
            this._outAnimation('in'); //
        },
        leave(el,done) { //
            const {x,y,imgScale} = this._getPosition();
            let animation = {
                0: {
                    transform: `translate3d(0,0,0) scale(1)`  
                },
                100: {
                    transform: `translate3d(-${x}px,${y}px,0) scale(${imgScale})`
                }
            };
            this._inAnimation('out',animation,done); //
        },
        afterLeave() {
            this._outAnimation('out');
        },
        _inAnimation(name,animation,done) { //create-keyframe-animationnpm 
            animations.registerAnimation({ //api
                name: name,
                animation,
                presets: {
                    duration: 400,
                    easing: 'linear'
                }
            });
            // done--afterEnterenter()
            animations.runAnimation(this.$refs.cdWrapper, name ,done);//
        },
        _outAnimation(name) {//
            animations.runAnimation(name);
            this.$refs.cdWrapper.style.animation = ''; //
        },
        _getPosition() {  //
            const minWidth = 0.8 * this.fontSize;    
            const minHeight = 0.8 * this.fontSize;
            const minLeft = 0.8 * this.fontSize;
            const minBottom = 0.4 * this.fontSize;
            const maxTop = 1.7 * this.fontSize;
            const maxHeight = 6 * this.fontSize;
            const imgScale = minHeight / maxHeight ; //
            const x = window.innerWidth / 2 - minLeft -minWidth / 2 ;  //
            const y = window.innerHeight - ( maxHeight / 2 + maxTop + minBottom + minHeight / 2)
            return {x,y,imgScale}
        },

        /* ----------------------------------------------------------- */
        touchstart(e) {
            this.touch.flag = true;
            const touch = e.touches[0];
            this.touch.startX = touch.pageX;
            this.touch.startY = touch.pageY;
        },
        touchmove(e) {
            if(!this.touch.flag) {
                return;
            }

            const touch = e.touches[0];
            let moveX = touch.pageX - this.touch.startX;//moveX>0,moveX<0,
            let moveY = touch.pageY - this.touch.startY;

             if(Math.abs(moveX) < Math.abs(moveY)) {  //
                this.touch.flag = false ;
                return;
             }

            const lyricLeft = this.currentDot === 'cd' ? 0 : -1*window.innerWidth; //
            //cd-1*window.innerWidth
            //0
            let lyricMoveX = Math.min(0,Math.max(-1*window.innerWidth,moveX+lyricLeft));
            this.$refs.scroll.$el.style[transform] = `translate3d(${lyricMoveX}px,0,0)`;

            //
            this.touch.percent = Math.abs(lyricMoveX) / window.innerWidth *100 ; //
            this.$refs.scroll.$el.style[transitionDuration] = 0;
            this.$refs.middle_l.style.opacity = 1 - this.touch.percent/100;
            this.$refs.middle_l.style[transitionDuration] = 0;
        },
        touchend() {
            //touchstarttouchendclickcd
            //0
            if(this.touch.percent === 0) {
                this.touch.flag = false;
                return ;
            }


            let moveX;
            let opacity; 
            const scroll = this.$refs.scroll.$el;
            const middle_l = this.$refs.middle_l;

            if(this.currentDot === 'cd') {  //cd0%,10%cd
                if(this.touch.percent > 10) {
                    moveX = -1*window.innerWidth; 
                    opacity = 0;
                    this.currentDot ='lyric';
                } else {
                    moveX = 0;
                    opacity = 1;
                }
            } else if (this.currentDot === 'lyric') { //
                if(this.touch.percent < 90) {//100%,90%cd10%
                    moveX = 0; 
                    opacity = 1;
                    this.currentDot ='cd';
                } else {
                    moveX = -1*window.innerWidth;
                    opacity = 0;
                }
            }
 
            scroll.style[transform] = `translate3d(${moveX}px,0,0)`;
            scroll.style[transitionDuration] = `300ms`;
            middle_l.style.opacity = opacity;
            middle_l.style[transitionDuration] = `300ms`;

           //
            this.touch.flag = false;
            this.touch.percent = 0;
        },

        /* --------------------------VUEX-------------------------------------- */
        ...mapMutations({
            setFullScreen: 'SET_FULL_SCREEN',
           /*  setPlaying: 'SET_PLAYING', */
            /* setCurIndex: 'SET_CUR_INDEX',
            setPlayMode: 'SET_PLAY_MODE',
            setPlayList: 'SET_PLAY_LIST' */
        }),
        ...mapActions([
            'savePlayingSong'
        ])
    },
    components: {
        'v-progressBar': ProgressBar,
        'v-progressCircle': ProgressCircle,
        'v-scroll': Scroll,
        'v-playList': PlayList
    }
}
</script>

<style lang="stylus" scoped>
@import '~styles/variable';
@import '~styles/mixin';

/* playpauseclass, */
@keyframes rotate  
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)

.maxPlayer
    fixed()
    background: $color-background
    &.max-enter-active,
    &.max-leave-active
        transition: all .4s
        .maxPlayer-header,
        .maxPlayer-footer
            transition: all .4s
    &.max-enter,
    &.max-leave-to
        opacity: 0
        .maxPlayer-header
            transform: translate3d(0,-100%,0)
        .maxPlayer-footer
            transform: translate3d(0,100%,0) 
    .maxPlayer-bgImg
        absolute()
        opacity: .6
        z-index: -1
        filter: blur(20px)
        .bgImg
            width: 100%
            height: 100%
    .maxPlayer-header
        width: 100%
        .iconfont
            position: absolute
            width: .88rem
            height: .88rem
            line-height: .88rem
            text-align:center
            color: $color-theme
            font-size: $font-size-large-x
            transform: rotate(-90deg)
        .maxPlayer-title
            height: .88rem
            line-height: .88rem
            text-align: center
            font-size: $font-size-large
            color: $color-text
        .maxPlayer-name
            height: .4rem
            line-height: .4rem
            text-align: center
            margin-bottom: .5rem
    .maxPlayer-middle
        position: absolute
        top: 1.7rem
        bottom: 3.4rem
        width: 100%
        .middle-l
            .cd-wrapper
                position: relative
                width: 6rem
                height: 6rem
                margin: 0 auto
                box-sizing: border-box
                z-index:1
                .cd
                    width: 100%
                    height: 100%
                    border-radius: 50%
                    &.play /* CSS3 */
                        animation: rotate 20s linear infinite
                    &.pause     /* CSS3 */
                        animation-play-state: paused
                    .cd-Img
                        position: absolute
                        width: 100%
                        height: 100%
                        box-sizing: border-box
                        border: .2rem solid hsla(0,0%,100%,.1)
                        border-radius: 50%
            .play-lyric-wrapper
                overflow: hidden
                width: 80%
                margin: .6rem auto 0
                text-align: center
                color: $color-text-l
        .middle-r
            absolute()
            left: 100%
            width: 100%
            height: 100%
            overflow: hidden
            transform: translate3d(0,0,0)
            .lyric-wrapper
                .lyric
                    text-align: center
                    color: $color-text-l
                    .lyric-list
                        line-height: .64rem
                        &.activeLyric
                            color: $color-text
    .maxPlayer-footer
        position: absolute
        bottom: 1rem
        width: 100%
        .dot-wrapper
            font-size: 0
            text-align:center
            .dot
                display: inline-block
                width: .16rem
                height: .16rem
                background: $color-text-l
                border-radius: 50%
                margin: 0 .1rem
                &.active
                    width: .4rem
                    border-radius: 100px
                    background: $color-text-ll
        .progress-wrapper
            width: 80%
            height: 1rem
            margin: 0 auto
            flex-between()
            .time
                flex: 0 0 .7rem
                width: .7rem
                height: .6rem
                line-height: .6rem
                font-size: $font-size-small
                &.start-time            
                    text-align: left
                &.end-time
                    text-align: right
            .progress-bar-wrapper
                flex: 1
    .maxPlayer-btn  
        flex-center()
        .icon
            flex: 1
            font-size: .6rem
            color: $color-theme
            &.i-left
                text-align: right
            &.i-center
                text-align: center
                padding: 0 .4rem
                font-size: .8rem
            &.i-right
                text-align: left
            &.errorClass
                color: $color-theme-d
.minPlayer
    position: fixed
    bottom: 0
    width: 100%
    height: 1.2rem
    background: $color-highlight-background
    flex-center()
    .minPlayer-bgImg
        width: .8rem
        height: .8rem
        flex: 0 0 .8rem
        padding: 0 .2rem 0 .4rem
        .cd-wrapper
            position: relative
            width: .8rem
            height: .8rem
            margin: 0 auto
            .cd
                width: 100%
                height: 100%
                border-radius: 50%
                &.play /* CSS3 */
                    animation: rotate 20s linear infinite
                &.pause     /* CSS3 */
                    animation-play-state: paused
                .cd-Img
                    position: absolute
                    width: 100%
                    height: 100%
                    border-radius: 50%
    .minPlayer-desc
        flex: 1
        line-height: .4rem
        .name
            name: $color-text
        .singer
            font-size: $font-size-small
            color: $color-text-l
    .icon
        width: .6rem
        flex: 0 0 .6rem
        padding: 0 .2rem
        color: rgba(255,205,49,.5)
        .icon-playlist
            font-size: .6rem
        .icon-play,
        .icon-pause
            position: absolute
            top: 0
            left: 0
            font-size: .64rem

</style>



