//singerList

export default class Singer {
    constructor({mid,name}) {   //constructor
        this.mid = mid;
        this.name =name;
        this.avatar = `http://y.gtimg.cn/music/photo_new/T001R300x300M000${this.mid}.jpg?max_age=2592000`;
    }
} 


