<template>
    <div class="progress-circle">
        <svg viewBox='0 0 100 100' version='1.1' xmlns="http://www.w3.org/2000/svg" class='svg'>
            <circle class='circle circle-bg' cx='50' cy='50' r='50' fill='transparent' />  <!--  -->
             <!-- dasharraydashoffset  -->
            <circle class='circle circle-line' cx='50' cy='50' r='50' fill='transparent'
                    :stroke-dasharray='dasharray'
                    :stroke-dashoffset='dashoffset' /> 
        </svg>
        <slot></slot>
    </div>
</template>

<script>
/* stroke-dashoffset */
export default {
    data() {
        return {
            dasharray:Math.PI * 100   //*
        }
    },
    props: {
        speed: {  //
            type: Number,
            default: 0
        }
    },
    computed: {
        dashoffset() {  // 
            return this.dasharray - this.dasharray*(this.speed/100);
        }
    }
}
</script>

<style lang="stylus" scoped>
@import '~styles/variable';

.progress-circle
    position: relative
    .svg
        width: .65rem
        height: .65rem
        circle
            stroke-width: .16rem
            transform-origin: center
            &.circle-bg
                transform: scale(0.9) 
                stroke: $color-theme-d
            &.circle-line
                transform: scale(0.9) rotate(-90deg)
                stroke: $color-theme

</style>



